import request from '@/utils/request';
var api = {
  GetAll: '/api/DMS/NFC/GetAll',
  Create: '/api/DMS/NFC/create',
  UpdateInfo: '/api/DMS/NFC/UpdateInfo',
  Delete: '/api/DMS/NFC/delete',
  GetById: '/api/DMS/NFC/GetById',
  GetList: '/api/DMS/NFC/GetList'
};
/**
 * @description 获取全部NFC信息
 *
 */

export function GetAll() {
  return request({
    url: api.GetAll,
    method: 'get'
  });
}
/**
 * @description 根据关键字模糊查询
 *
 */

export function GetList(obj) {
  return request({
    url: api.GetList,
    method: 'get',
    params: obj
  });
}
/**
 * @description 新增NFC信息
 *
 */

export function Create(obj) {
  return request({
    url: api.Create,
    method: 'post',
    data: obj
  });
}
/**
 * @description 修改NFC信息
 *
 */

export function UpdateInfo(obj) {
  return request({
    url: api.UpdateInfo,
    method: 'post',
    data: obj
  });
}
/**
 * @description 删除NFC信息
 *
 */

export function Delete(id) {
  return request({
    url: api.Delete,
    method: 'post',
    data: {
      id: id
    }
  });
}
/**
 * @description 根据ID获取NFC信息
 *
 */

export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    params: {
      id: id
    }
  });
}