import Detail from "./detail";
import { GetList, Delete } from '@/api/equipmentManagement/nfc';
import Search from '@/components/search';
import { pagging, buttonPermissions } from '@/mixin';
export default {
  name: 'Nfc',
  mixins: [pagging, buttonPermissions],
  components: {
    Search: Search,
    Detail: Detail
  },
  data: function data() {
    return {
      loading: false,
      tableData: [],
      detailVisible: false
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    detailHidden: function detailHidden() {
      this.detailVisible = false;
      this.init();
    },
    changeKeyword: function changeKeyword(val) {
      this.keyword = val;
    },
    searchInit: function searchInit() {
      this.pageIndex = 1;
      this.init();
    },
    init: function init() {
      var _this = this;

      this.loading = true;
      GetList({
        keyword: this.keyword,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }).then(function (res) {
        _this.loading = false;

        _this.$set(_this, 'tableData', res.data.items);

        _this.totalCount = res.data.totalCount;
        _this.pageIndex = res.data.pageIndex;
        _this.pageSize = res.data.pageSize;
      });
    },
    edit: function edit(id) {
      var _this2 = this;

      this.detailVisible = true;
      this.$nextTick(function () {
        _this2.$refs.detail.init(id);
      });
    },
    del: function del(id) {
      var _this3 = this;

      Delete(id).then(function () {
        _this3.$message({
          type: "success",
          message: '删除成功',
          duration: 500,
          onClose: function onClose() {
            _this3.pageIndex = 1;

            _this3.init();
          }
        });
      });
    },
    insert: function insert() {
      var _this4 = this;

      this.detailVisible = true;
      this.$nextTick(function () {
        _this4.$refs.detail.init();
      });
    }
  }
};